import React, { Component } from "react";
import { Layout } from "antd";


const { Footer } = Layout;

class TheFooter extends Component {
    
    

    render() {
        return (
            <Footer style={{backgroundColor:'#112a45'}}>
                <center>
                <h4 style={{ lineHeight: '12px', justifyContent: 'center', display:'flex', color:'white' }}>2023 - SpiceCap</h4>
                </center>
                
            </Footer>
        )
    }


}

export default TheFooter; 